.item-container{
    width:300px;
    height:300px;
    border:3px solid black;
    position:relative;
    border-radius:10px;
    box-shadow:3px 3px 5px rgba(0,0,0,0.3);
    cursor:pointer;
    transition:linear 0.1s;

}
.item-container:hover{
    box-shadow:8px 8px 8px rgba(0,0,0,0.3);
}
.item-image{
    width:300px;
    height:200px;
    position: absolute;
    top:0px;left:0px;
    background:linear-gradient(45deg,orange,orange,yellow,yellow);
    border-top-right-radius:10px;
    border-top-left-radius:10px;
    border-bottom:3px solid black;
}
.item-price{
    width:130px;
    height:90px;
    position:absolute;
    bottom:5px;right:5px;
    font-size:20px;
    color:white;
    background:linear-gradient(180deg,#5bf15b,#0cdd0c);
    border-radius:10px;
}
.item-name{
    position:absolute;
    bottom:5px;left:3px;
    font-family:cursive;
    font-size:25px;
    text-shadow:2px 2px yellow,
    -2px -2px orange;
}