*{
    outline:none;
}
.App{
    overflow-x:hidden;
}
.search-input{
    width:300px;
    height:30px;
    position: absolute;
    left:50%;
    top:155px;
    margin:-150px;
    border:none;
    border-radius:10px;
    box-shadow:2px 2px 2px inset rgba(0,0,0,0.3);
}
.search-bar{
    width:100%;
    height:50px;
    background:linear-gradient(45deg,orange,orange,yellow,yellow);
    position: fixed;
    top:0px;left:0px;
    box-shadow:3px 5px 3px rgba(0,0,0,0.3);
}