.items-container{
    display:grid;
    grid-template-columns:repeat(3,300px);
    grid-gap:30px;
    position: absolute;
    top:100px;
}
@media  (orientation:portrait){
    .items-container{
        grid-template-columns:repeat(1,300px);
        left:50%;top:230px;
        margin:-150px;
    }    
}